import Vue from 'vue'
// import './public-path'
import 'normalize.css'
import router, { destroy } from './router'
import 'material/config/axios'
import { destroy as scrollDestory } from 'material/lib/scroll'
import initAxios from '@/config/axios'
import {
  Tag,
  Icon,
  Search,
  Notify,
  Loading,
  Empty,
} from 'material/ui'
import App from './App.vue'
import 'material/ui/index.less'
import './less/index.less'

Vue.use(Tag)
Vue.use(Icon)
Vue.use(Search)
Vue.use(Notify)
Vue.use(Loading)
Vue.use(Empty)

let app = null

function render(props = {}) {
  const { container } = props
  app = new Vue({
    router,
    beforeDestroy() {
      scrollDestory()
    },
    render: h => h(App),
  }).$mount(container ? container.querySelector('#app') : '#app')
}

if (!window.__POWERED_BY_QIANKUN__) {
  render()
}

/**
 * bootstrap  mount  bootstrap
 *  unmount 
 */
export async function bootstrap() {
  console.log('[vue] vue app bootstraped')
}

/**
 *  mount 
 */
export async function mount(props) {
  initAxios()
  const { container } = props
  container.style.height = '100%'
  console.log('[vue] props from main framework', props)
  render(props)
}

/**
 *  / 
 */
export async function unmount() {
  app.$destroy()
  app = null
  destroy()
}

console.log('hello world')

if (module && module.hot) {
  module.hot.accept()
}
